    <div class="storage-top-storage">
      <div style="display:flex">
        <div class="top-year-storage">
          <span>领购年份：</span>
          <select>
            <option>全部</option>
            <option>{{yushe.month.substr(0,4)}}年</option>
          </select>
        </div>
        <div class="top-year-storage">
          <span>领购月份：</span>
          <select>
            <option>全部</option>
            <option>{{yushe.month.substr(-2,2)}}月</option>
          </select>
        </div>
        <div class="top-classify-storage">
          <span>发票种类：</span>
          <select>
            <option>全部</option>
            <option>增值税专用发票</option>
            <option>增值税普通发票</option>
          </select>
        </div>
      </div>
      <div class="top-pic-content-storage">
        <div class="top-pic-storage">
          <div class="notice1"></div>
          <div class="notice1"></div>
          <div class="notice1"></div>
          <div class="notice1"></div>
          <div id="top-pic-chose-storage" @click="click_exit"></div>
        </div>
      </div>
    </div>

    <table
      cellpadding="0px"
      cellspacing="0px"
      class="storage-content-table"
      style="width: 100%;height: auto;border: solid #adaa9c;border-width: 1px 0px 0px 1px"
    >
      <tbody>
      <tr>
        <th>发票种类</th>
        <th>开票限额</th>
        <th>类别代码</th>
        <th>类别名称</th>
        <th>起始号码</th>
        <th>发票张数</th>
        <th>卷终止号</th>
        <th>领购日期</th>
      </tr>
      <tr>
        <td>增值税普通发票</td>
        <td>{{yushe.po_xiane}}</td>
        <td>{{yushe.po_code}}</td>
        <td>xx省增值税普通发票</td>
        <td>{{yushe.po_code_start}}</td>
        <td>{{yushe.po_number}}</td>
        <td>{{yushe.po_code_end}}</td>
        <td>{{yushe.month + '-01'}}</td>
      </tr>
      <tr style="background-color: #f7fbff;color: #000000">
        <td>增值税专用发票</td>
        <td>{{yushe.zhuan_xiane}}</td>
        <td>{{yushe.zhuan_code}}</td>
        <td>xx省增值税专用发票</td>
        <td>{{yushe.zhuan_code_start}}</td>
        <td>{{yushe.zhuan_number}}</td>
        <td>{{yushe.zhuan_code_end}}</td>
        <td>{{yushe.month + '-01'}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    computed: {
      yushe () {
        let a = this.$store.state.service.serviceData
        a.po_number = parseInt(a.po_code_end) - parseInt(a.po_code_start)
        a.zhuan_number = parseInt(a.zhuan_code_end) - parseInt(a.zhuan_code_start)
        return a
      }
    },
    methods: {
      click_exit () {
        this.$router.go(-1)
      }
    }
  }
</script>

